body{
    margin: 0;
    padding: 0;
}


.containerHistory{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 30px;

 
}
.roomName
{
    font-size:25px;
    font-weight: bold;}

.sensorType{
  font-size:20px;
  font-weight: bold;
}
.mainDivHistory{
    display: flex;
    align-items: center;
    flex-direction:column;
    justify-content: start;
    width: 45%;
    margin: 10px;
    border-bottom: 2px solid;

}
.headerDivHistory{
    display: flex;
    width: 100%;
    align-items: center;
    border-radius: 25px;
    justify-content: start;
}
.historyInfo{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    justify-items: start;
    width: 100%;
}

.temperature,
.humidity{
    display: flex;
    flex-direction: column;
    margin: 10px;
    
    
}


.temperatureHeader,
.humidityHeader{
    margin-bottom: 5px;
    
}


.humidityDivHistory,
.temperatureDivHistory{
    display: flex;
    flex-direction: column;
    width: 100px;
    
}

@media only screen and (max-width: 500px) {
    .roomName
   {
    font-size:20px;
    font-weight: bold;}

.sensorType{
  font-size:15px;
  font-weight: bold;
}

  }

  @media only screen and (max-width: 300px) {
    .roomName
   {
    font-size:15px;
    font-weight: bold;}

.sensorType{
  font-size:12px;
  font-weight: bold;
}

  }

  @media only screen and (max-width: 800px) {
    .mainDivHistory{
        width: 80%;
    }
  }

